export const generateLoginTemplate = (): string => `
  <div>
    <form id="login-form" class="authentication-form">
      <label class="authentication-label" for="login-email"></label>
      <input id="login-email" class="authentication-input" type="email" placeholder="woowacourse@gmail.com"/>
      <label class="authentication-label" for="login-password"></label>
      <input id="login-password" class="authentication-input" type="password" placeholder=" ." minlength="8" maxlength="16"/>
      <button class="input-form-button authentication-button"></button>
    </form>
    <p class="not-yet-member">
        ? <a href="#register" class="not-yet-member-link"> </a>
    </p>
  </div>
`;

export const generateUserInfoTemplate = (userEmail?: string, isUserInfoEdit = false): string => `
  <form id="user-info-form" class="authentication-form">
    <label class="authentication-label" for="user-info-email"></label>
    <input id="user-info-email" class="authentication-input" type="email" placeholder="  ." value="${
      isUserInfoEdit ? userEmail : ''
    }" ${isUserInfoEdit ? 'disabled' : ''}/>
    <label class="authentication-label" for="user-info-name"></label>
    <input id="user-info-name" class="authentication-input" type="text" placeholder=" ." minlength="2" maxlength="6"/>
    <label class="authentication-label" for="user-info-password"></label>
    <input id="user-info-password" class="authentication-input" type="password" placeholder=" ." minlength="8" maxlength="16"/>
    <label class="authentication-label" for="user-info-verification-password"> </label>
    <input id="user-info-verification-password" class="authentication-input" type="password" placeholder=" ." minlength="8" maxlength="16"/>
    <button class="input-form-button authentication-button"></button>
  </form>
`;
