export const generateItemManageTableRowTemplate = ({ itemName, itemPrice, itemQuantity }) => `
    <tr data-item-name="${itemName}">
      <td><input class="item-info-input-cell" value="${itemName}" type="text" minlength="1" maxlength="10" disabled/></td>
      <td><input class="item-info-input-cell" value="${itemPrice}" type="number" min="100" max="10000" step="10" disabled/></td>
      <td><input class="item-info-input-cell" value="${itemQuantity}" type="number" min="1" max="20" step="1" disabled/></td>
      <td class="item-button-cell">
        <div>
          <button type="button" class="default-button edit-item-button"></button>
          <button type="button" class="default-button delete-item-button"></button>
        </div>
      </td>
      <td class="item-button-cell hide">
        <button type="button" class="default-button confirm-item-button"></button>
      </td>
    </tr>
`;

export const generateItemManageTabContentTemplate = (itemList) => `
    <form id="item-info-form" class="input-form">
      <label>   .</label>
      <div class="input-form-container">
        <input class="item-info-input" type="text" placeholder="" minlength="1" maxlength="10" autofocus/>
        <input class="item-info-input" type="number" placeholder="" min="100" max="10000" step="10"/>
        <input class="item-info-input" type="number" placeholder="" min="1" max="20" step="1"/>
        <button class="input-form-button"></button>
      </div>
    </form>
    <div class="table-container">
      <table class="item-status-table">
        <caption><h2> </h2></caption>
        <tr>
          <th><span></span></th>
          <th><span></span></th>
          <th><span></span></th>
          <th><span></span></th>
        </tr>
        ${itemList.map((itemInfo) => generateItemManageTableRowTemplate(itemInfo)).join('')}
      </table>
    </div>
`;

export const generateCoinRechargeTabContentTemplate = (chargedAmount, coinCollection) => `
    <form id="cash-charge-form" class="input-form">
        <label>   .</label>
        <div class="input-form-container">
          <input class="cash-charge-input" type="number" placeholder="" min="10" max="100000" step="10" autofocus/>
          <button class="input-form-button"></button>
        </div>
    </form>
    <p class="vendingmachine-total-amount">  : <span id="charged-amount">${chargedAmount}</span></p>
    <table class="vendingmachine-coin-table">
      <caption><h2>  </h2></caption>
      <tr>
        <th></th>
        <th></th>
      </tr>
      <tr>
        <td>500</td>
        <td class="coin-count" data-coin-value="500">${coinCollection[500]}</td>
      </tr>
      <tr>
        <td>100</td>
        <td class="coin-count" data-coin-value="100">${coinCollection[100]}</td>
      </tr>
      <tr>
        <td>50</td>
        <td class="coin-count" data-coin-value="50">${coinCollection[50]}</td>
      </tr>
      <tr>
        <td>10</td>
        <td class="coin-count" data-coin-value="10">${coinCollection[10]}</td>
      </tr>
    </table>
`;

export const generateItemPurchaseTableDataTemplate = ({ itemName, itemPrice, itemQuantity }) => `
    <td>${itemName}</td>
    <td>${itemPrice}</td>
    <td>${itemQuantity}</td>
    <td class="item-button-cell">
      <button type="button" class="default-button purchase-item-button"
        ${itemQuantity === 0 ? 'disabled' : ''}
      ></button>
    </td>
`;

export const generateItemPurchaseTabContentTemplate = (itemList, chargedCash) => `
  <form id="cash-charge-form" class="input-form">
    <label>   .</label>
    <div class="input-form-container">
      <input class="cash-charge-input" type="number" placeholder="" min="10" max="10000" step="10" autofocus/>
      <button class="input-form-button"></button>
    </div>
  </form>
  <p class="vendingmachine-total-amount"> : <span id="charged-amount">${chargedCash}</span></p>
  <div class="table-container">
    <table class="item-status-table">
      <caption><h2> </h2></caption>
      <tr>
        <th><span></span></th>
        <th><span></span></th>
        <th><span></span></th>
        <th><span></span></th>
      </tr>
      ${itemList
        .map((item) => {
          return `
          <tr data-item-name="${item.itemName}">
            ${generateItemPurchaseTableDataTemplate(item)}
          </tr>
        `;
        })
        .join('')}
    </table>
  </div>
  <table class="coin-return-table">
    <caption><h2> </h2></caption>
    <tr>
      <th></th>
      <th></th>
    </tr>
    <tr>
      <td>500</td>
      <td class="coin-count" data-coin-value="500">${0}</td>
    </tr>
    <tr>
      <td>100</td>
      <td class="coin-count" data-coin-value="100">${0}</td>
    </tr>
    <tr>
      <td>50</td>
      <td class="coin-count" data-coin-value="50">${0}</td>
    </tr>
    <tr>
      <td>10</td>
      <td class="coin-count" data-coin-value="10">${0}</td>
    </tr>
  </table>
  <button class="default-button return-button"></button>
`;

export const generateConfirmMessage = (itemName) => ` '${itemName}'  ?`;
